.Container {
  display: flex;
  flex-direction: column;
  gap: 1rem; }
  .Container .List {
    display: flex;
    gap: 3rem; }
    .Container .List a {
      font-size: 1.5rem;
      font-weight: bold;
      transition: color 200ms ease;
      color: grey; }
      .Container .List a[data-state='active'] {
        color: white; }
      .Container .List a[data-state='inactive']:hover {
        color: #9a9a9a; }
