.zui-loading-indicator {
  display: flex; }
  .zui-loading-indicator-bottom, .zui-loading-indicator-top {
    gap: 2.5rem;
    align-items: center; }
  .zui-loading-indicator-left, .zui-loading-indicator-right {
    gap: 1rem;
    align-content: flex-start;
    align-items: center; }
  .zui-loading-indicator-bottom {
    flex-direction: column-reverse; }
  .zui-loading-indicator-top {
    flex-direction: column; }
  .zui-loading-indicator-left {
    flex-direction: row; }
  .zui-loading-indicator-right {
    flex-direction: row-reverse;
    justify-content: flex-end; }
  .zui-loading-indicator-text {
    font-size: 1rem;
    padding: 0;
    text-align: center; }
