.ArrowLink {
  position: relative; }
  .ArrowLink .ArrowContainer {
    position: absolute;
    left: calc(100% + 0.25rem);
    top: 0;
    height: 100%;
    width: 0.75rem;
    transition: margin-left 0.3s ease-in-out; }
    .ArrowLink .ArrowContainer .Arrow {
      height: 1px;
      width: 0.5rem;
      position: absolute;
      left: 0;
      top: 50%;
      background: #52cbff; }
      .ArrowLink .ArrowContainer .Arrow::after, .ArrowLink .ArrowContainer .Arrow::before {
        content: ' ';
        position: absolute;
        right: -1px;
        height: 1px;
        width: 0.35rem;
        background: inherit; }
      .ArrowLink .ArrowContainer .Arrow::after {
        transform: translateY(-200%) rotate(45deg); }
      .ArrowLink .ArrowContainer .Arrow::before {
        transform: translateY(200%) rotate(-45deg); }
    .ArrowLink .ArrowContainer.Back {
      left: calc(100% + 0.5rem); }
      .ArrowLink .ArrowContainer.Back .Arrow {
        transform: rotate(180deg); }
    .ArrowLink .ArrowContainer::after {
      content: ' ';
      position: absolute;
      top: 50%;
      left: 50%;
      height: 0;
      width: 0;
      background: #000000;
      transform: translate(-60%, -50%);
      transition: box-shadow 0.1s ease-in-out; }
  .ArrowLink:hover .ArrowContainer {
    margin-left: 0.25rem; }
    .ArrowLink:hover .ArrowContainer::after {
      box-shadow: 0px 0px 10px 3px #52cbff; }
  .ArrowLink:hover .Back {
    margin-left: -0.25rem; }
