.zui-dropdown-trigger {
  background: none;
  color: #ffffff;
  border: none;
  cursor: pointer; }

.zui-dropdown-menu {
  padding: 1rem;
  background: #1a1022;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  border-radius: 1rem;
  border: 1px solid #472e5b;
  transform-origin: var(--radix-dropdown-menu-content-transform-origin); }
  .zui-dropdown-menu .zui-dropdown-item {
    user-select: none;
    cursor: pointer;
    color: #ffffff; }
