.Container {
  width: 100%; }
  .Container th {
    font-size: 0.75rem;
    font-weight: bold;
    color: #bfbfbf;
    padding: 1rem; }
  .Container th,
  .Container td {
    padding: 1rem;
    vertical-align: middle; }
    .Container th:first-of-type,
    .Container td:first-of-type {
      padding-left: 0; }
    .Container th:last-of-type,
    .Container td:last-of-type {
      padding-right: 0; }
  .Container .Right {
    margin-left: auto;
    text-align: right; }
  .Container .Center {
    margin: 0 auto;
    text-align: center; }
  .Container .Left {
    margin-right: auto;
    text-align: left; }
