.StepBar {
  position: relative;
  width: 100%;
  font-size: 0.75rem;
  font-weight: 700;
  text-align: center; }
  .StepBar::before {
    content: ' ';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    border-bottom: 1px solid #232323; }
  .StepBar .Step {
    cursor: pointer;
    transition: opacity 0.7s ease-in-out;
    z-index: 2; }
    .StepBar .Step > a {
      color: #52cbff;
      transition: text-shadow 0.1s ease-in-out; }
  .StepBar .Bar {
    padding-bottom: 4px;
    color: #52cbff;
    border-bottom: 1px solid #52cbff;
    transition: all 0.7s ease-in-out; }
    .StepBar .Bar::after {
      content: attr(data-text); }
    .StepBar .Bar.Hide {
      border-bottom: 1px solid #000000;
      color: #000000;
      z-index: -1; }
