.zui-wizard {
  display: flex;
  flex-direction: column;
  width: 100%;
  box-sizing: border-box;
  gap: 2.5rem; }
  .zui-wizard .zui-wizard-header {
    width: 100%;
    text-align: center; }
    .zui-wizard .zui-wizard-header * {
      text-align: center; }
    .zui-wizard .zui-wizard-header h1 {
      font-size: 1.5rem;
      font-weight: 700; }
    .zui-wizard .zui-wizard-header h2 {
      margin-top: 0.75rem;
      font-size: 1rem;
      font-weight: 400; }
    .zui-wizard .zui-wizard-header hr {
      margin-top: 1.5rem;
      margin-bottom: 0;
      height: 1px;
      background-color: #808080;
      border: none; }
  .zui-wizard .zui-wizard-loading > p:first-of-type {
    margin-top: 0; }
