.zui-tooltip__trigger {
  display: inline-block;
  margin: unset; }

.zui-tooltip__content {
  padding: 1.5rem;
  max-width: 22rem;
  background-color: #23142f;
  border-radius: 16px; }
  .zui-tooltip__content[data-side='right'][data-align='end'] {
    border-bottom-left-radius: unset; }
  .zui-tooltip__content[data-side='right'][data-align='start'] {
    border-top-left-radius: unset; }
  .zui-tooltip__content[data-side='left'][data-align='end'] {
    border-bottom-right-radius: unset; }
  .zui-tooltip__content[data-side='left'][data-align='start'] {
    border-top-right-radius: unset; }

.zui-tooltip__arrow {
  fill: #23142f; }

.zui-tooltip__storycard {
  min-height: 500px;
  display: flex;
  align-items: center;
  justify-content: center; }
  .zui-tooltip__storycard > * {
    margin: unset; }
